import common from '../../../../common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			name:'aa',
			userInfo:[],		//用户信息
			hotline:'',
			maishou:'',
			todayOrderNums:0,
			orderNums:0,
		}
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		
		console.log("userInfo1",userInfo)
		if(userInfo == '' || userInfo == null || typeof(userInfo) == undefined){
			uni.navigateTo({
				url: '/pages/user/shop/userlogin/userlogin',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}else{
			that.userInfo = userInfo;
			that.getUserInfo();
				
			that.getMerchantContact();
			that.getMerchantOrderInfo();
		}
		
		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		
		that.getUserInfo();
		that.getMerchantContact();
		that.getMerchantOrderInfo();
		var userInfo = uni.getStorageSync('userInfo');
		console.log("userInfo",userInfo)
		if(userInfo.shop_status == 0){
			uni.navigateTo({
				url: '/pages/user/order',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		//核销
		hexiaoOrder(){
			uni.navigateTo({
				url: '/pages/index/hexiao',
			});
		},
		//店铺打烊
		changeShopShundown(e){
			let that = this
			let attr = e.currentTarget.dataset.attr
			let shutdown = that.userInfo.shop_shundown
			var userInfo = uni.getStorageSync('userInfo');
			console.log(shutdown);
			console.log(attr);
			if(attr == shutdown){
				return false
			}else{
				uni.request({
					url: common.apiUrl + 'merchant/changeShopShundown',
					method: 'GET',
					data: {
						shopid:userInfo.shop_id,
						shutdown:attr
					},
					success: res => {
						// let data = res.data
						// let userInfo = data.userInfo
						// uni.setStorageSync('userInfo',userInfo);
						// that.userInfo = userInfo
						that.getUserInfo();
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		//获取商家订单统计信息
		getMerchantOrderInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var shopid = userInfo.shop_id
			uni.request({
				url: common.apiUrl + 'merchant/getMerchantOrderInfo',
				method: 'GET',
				data: {
					shopid:shopid
				},
				success: res => {
					that.todayOrderNums = res.data.todayOrderNums
					that.orderNums = res.data.orderNums
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取联系信息
		getMerchantContact(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var marketid = userInfo.shop_marketid
			uni.request({
				url: common.apiUrl + 'merchant/getMerchantContact',
				method: 'GET',
				data: {
					marketid:marketid
				},
				success: res => {
					console.log(res)
					that.hotline = res.data.hotline
					that.maishou = res.data.maishou
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//资金提现
		tixian(){
			var that = this
			console.log('aaa')
			uni.navigateTo({
				url: '/pages/index/recharge',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		//订单列表
		orderList(){
			var that = this
			console.log('aaa')
			uni.navigateTo({
				url: '/pages/index/orderlist',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		//特推订单列表
		superOrderList(){
			var that = this
			console.log('aaa')
			uni.navigateTo({
				url: '/pages/index/superorderlist',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		//绑定银行卡
		bindCard(){
			var that = this
			console.log('aaa')
			uni.navigateTo({
				url: '/pages/index/bindcard',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		//客服热线
		contactKefu(){
			var that = this
			var hotline = that.hotline
			uni.makePhoneCall({
				phoneNumber:hotline
			})
		},
		//联系买手
		contactMaishou(){
			var that = this
			var mobile = that.maishou.mem_mobile
			
			uni.makePhoneCall({
				phoneNumber:mobile
			})
		},
		
		//退出登录
		loginout(){
			var that = this
			uni.showModal({
				title:'提示',
				content:'确定要退出吗？',
				success: res => {
					if(res.confirm){
						uni.clearStorageSync('userInfo');
						uni.redirectTo({
							url: '/pages/userlogin/userlogin',
							success: res => {
								console.log('退出')
							},
							fail: () => {},
							complete: () => {}
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url
			uni.navigateTo({
				url: url
			});
		},
		
		//更新获取用户信息
		getUserInfo(){
			//console.log('获取用户信息');
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			console.log("userInfouserInfo",userInfo)
			uni.request({
				url: common.apiUrl + 'merchant/getUserInfo',
				method: 'GET',
				data: {
					shop_id:userInfo.shop_id
				},
				success: res => {
					var shop = res.data.userInfo
					if(shop != ''){
						that.userInfo = shop
						uni.setStorageSync('userInfo',shop);
						if(shop.shop_status == 0){
							uni.navigateTo({
								url: '/pages/user/order',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
	}
}
